body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  overflow: hidden;
  background-color: #EEE;

  -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */ /* Konqueror HTML */
       -moz-user-select: none; /* Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome and Opera */
}

@font-face {
    font-family: 'petita';
    src: url('resources/fonts/petita_bold.ttf');
}

#container {
  width: 100vw;
  height: 100vh;
  position: relative;
  -webkit-perspective: 800px;
          perspective: 800px;
}
#container-canvas {
  width: 100vw;
  height: 100vh;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  opacity: 1;
  -webkit-transition: opacity 2s ease-in, -webkit-transform 2s ease-in;
  transition: opacity 2s ease-in, -webkit-transform 2s ease-in;
  -o-transition: transform 2s ease-in, opacity 2s ease-in;
  transition: transform 2s ease-in, opacity 2s ease-in;
  transition: transform 2s ease-in, opacity 2s ease-in, -webkit-transform 2s ease-in;
}
#container-canvas.flipped {
  -webkit-transform: rotateX( -180deg );
          transform: rotateX( -180deg );
  opacity: 0;
}
#container-about {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: calc( 100vh - 48px );
  padding: 10px 30vw 0 30vw;
  font-family: 'petita';
  font-size: 18px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-transform: rotateX( 0deg );
          transform: rotateX( 0deg );
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  opacity: 1;
  overflow-y: auto;
  pointer-events: inherit;
}
#container-about.flipped {
  opacity: 0;
  -webkit-transform: rotateX( 180deg );
          transform: rotateX( 180deg );
  pointer-events: none;
}

#bml-canvas {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%,-50%);
      -ms-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  -webkit-box-shadow: rgba(0, 0, 0, 0.24) 0px 2px 10px, rgba(0, 0, 0, 0.36) 0px 2px 5px;
          box-shadow: rgba(0, 0, 0, 0.24) 0px 2px 10px, rgba(0, 0, 0, 0.36) 0px 2px 5px;
  -ms-interpolation-mode: nearest-neighbor;
      image-rendering: -webkit-optimize-contrast;
      image-rendering: -moz-crisp-edges;
      image-rendering: -o-pixelated;
      image-rendering: pixelated;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}


